import React, { Component } from 'react';

import {
  Button,
  Dropdown,
  Form,
  Header,
  Segment,
} from 'semantic-ui-react';

import ReactJson from 'react-json-view';

class FormFields extends Component {
  getInput = (field, idx) => {
    const { onChange, values } = this.props;
    const { name, type } = field;
    let value = this.formatValue(type, values[name]);
    let defaultInput = (
      <Form.Input
        autoFocus={(idx === 0)}
        value={value}
        label={`${name} [${type}]`}
        name={name}
        onChange={onChange}
      />
    );
    // an array of values
    if (type.substr(type.length - 2) === "[]") {
      let options = [];
      if (value && value.length > 0) {
        options = value.map((option) => ({
          key: option,
          value: option,
          text: option
        }))
      }
      defaultInput = (
        <Form.Select
          allowAdditions
          autoFocus={(idx === 0)}
          options={options}
          value={value}
          label={`${name} [${type}]`}
          name={name}
          selection
          search
          multiple
          onChange={onChange}
        />
      );
    }
    return (
      <Form.Field key={name}>
        {defaultInput}
      </Form.Field>
    );
  }
  formatValue = (type, value) => {
    switch (type) {
      case 'string': {
        try {
          // Try re-encoding to ensure it's not an object.
          return JSON.stringify(JSON.parse(value));
        } catch(err) {
          // no catch, ignore
        }
        // otherwise just stringify and return
        return value;
      }
      default: {
        return value;
      }
    }
    return value;
  }
  render() {
    const {
      fields,
      onChange,
      values,
    } = this.props;
    return (
      <Segment attached>
        <Segment basic>
          <Header>
            Field Data
          </Header>
          {fields.map((field, idx) => this.getInput(field, idx))}
        </Segment>
        <Segment inverted basic>
          <Header>
            Transaction Data
          </Header>
          <ReactJson
            displayDataTypes={false}
            displayObjectSize={false}
            iconStyle="square"
            name={null}
            src={values}
            style={{ padding: '1em' }}
            theme="harmonic"
          />
        </Segment>
      </Segment>
    );
  }
}

export default FormFields;
